.popup {
  visibility: hidden;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.5);
  transition: visibility 0.5s ease-out, opacity 0.5s ease-out;
}

.popup:hover {
  cursor: pointer;
}